.rat {
  color: #050505;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px; /* 128.571% */
}
.rating {
  display: flex;
  align-items: flex-end;
  font-size: 40px;
  line-height: 0.75;
}
.rating_body {
  position: relative;
}
.rating_body::before {
  display: flex;
  align-items: flex-start;
  gap: 2px;
}
.rating_active {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}
.rating_active::before {
  content: var(--star-active);
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  color: #eea10c;
}
.rating_items {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.rating_item {
  flex: 0 0 20%;
  opacity: 0;
  height: 100%;
}
.rating_value {
  font-size: 50%;
  line-height: 1;
  padding: 0px 0px 0px 10px;
}
.c-icon {
  --star-active: #fece3c;
  --star-inactive: #6c6962;
  fill: var(--star-inactive);
}

.c-icon.active {
  fill: var(--star-active);
}
